<template>
  <div class="wrap mod_address">
    <div class="item flex-center-y">
      <div class="title">收货地址：</div>
      <input type="text" placeholder="点击选择" readonly  :value="data.province !== '' ? data.province + ' ' + data.city + ' ' + data.district : ''"  @click="show=true">
      <van-icon name="arrow" class="arrow"/>
    </div>
    <div class="item flex-center-y">
      <div class="title">门牌号：</div>
      <input type="text" placeholder="详细地址，例：16号楼5层501室" v-model="data.address">
    </div>
    <div class="item flex-center-y">
      <div class="title">联系人：</div>
      <input type="text" placeholder="请填写收货人的姓名" v-model="data.name">
    </div>
    <div class="item flex-center-y">
      <div class="title"></div>
      <div class="flex_1">
        <van-radio-group v-model="data.famale" class="flex">
          <van-radio name="1" checked-color="#2198e6">先生</van-radio>
          <van-radio name="2" class="radio" checked-color="#2198e6">女士</van-radio>
        </van-radio-group>
      </div>
    </div>
    <div class="item flex-center-y">
      <div class="title">手机号：</div>
      <input type="number" placeholder="请填写收货人的手机号码" v-model="data.mobile">
    </div>
    <div class="checkbox_wrap">
      <van-checkbox v-model="is_default" checked-color="#2198e6">设置为默认地址</van-checkbox>
    </div>
    <div class="button flex-center" @click="submit">添加</div>

    <van-popup v-model="show" position="bottom">
      <van-area :area-list="site" @cancel="show=false" @confirm="changeAddress" />
    </van-popup>
  </div>
</template>

<script>
import { Icon, RadioGroup, Radio, Checkbox, Area, Popup, Toast } from 'vant';
import util from '../utils/utils.js'
export default {
  components: {
    'van-icon': Icon,
    'van-radio-group': RadioGroup,
    'van-radio': Radio,
    'van-checkbox': Checkbox,
    'van-area': Area,
    'van-popup': Popup
  },
  data() {
    return {
      checked: false,
      data: {
        name: '',
        province: '',
        city: '',
        district: '',
        address: '',
        mobile: '',
        famale: '1'
      },
      is_default: 0,
      address: [],
      site: util.site,
      show: false
    }
  },
  methods: {
     // 地址变动
    changeAddress(val) {
      this.data.province = val[0].name
      this.data.city = val[1].name
      this.data.district = val[2].name
      this.show = false
    },
    // 保存
    submit() {
      for(let i in this.data) {
        if(this.data[i] == '') {
          Toast('请信息检查是否填写完整')
          return false
        }
      }
      this.data.is_default = this.is_default ? '1' : '0'
      this.$post('user/receiving_address', this.data).then((res) => {
        Toast('保存成功')
        // this.$router.go(-1)
      })
    }
  }
}
</script>

<style scoped lang="scss">
.wrap {
  min-height: 100vh;
  background: #fff;
  padding: 0 35px;
}
.item {
  height: 110px;
  border-bottom: 1px solid #e1e1e1;
  font-size: 32px;
  .title {
    width: 180px;
  }
  input {
    flex: 1;
  }
  .radio {
    margin-left: 50px;
  }
}
.checkbox_wrap {
  margin: 30px 10px;
  font-size: 36px;
}
.button {
  margin: 44px auto;
  background: linear-gradient(to bottom, #2194e6 , #21cce6);
  color: #fff;
  height: 90px;
  border-radius: 60px;
  font-size: 38px;
  box-shadow: 0 4px 16px #b3edf6;
}
</style>



