<template>
  <div class="withdrawDeposit">
    <div class="way">
      <p class="title">选择提现方式</p>
      <div class="flex-between list">
        <div class="item flex-center active">
          <img src="../../assets/images/withdrawDeposit1.png" alt="">
          <p>支付宝</p>
        </div>
        <div class="item flex-center">
          <img src="../../assets/images/withdrawDeposit2.png" alt="">
          <p>微信</p>
        </div>
        <div class="item flex-center">
          <img src="../../assets/images/withdrawDeposit3.png" alt="">
          <p>银行卡</p>
        </div>
      </div>
    </div>
    <div class="cell flex-center-y">
      <div class="title">支付宝账号：</div>
      <input class="flex_1" type="text" placeholder="请输入支付宝账户">
    </div>
    <div class="cell flex-center-y">
      <div class="title">收款人姓名：</div>
      <input class="flex_1" type="text" placeholder="请输入收款人真实姓名">
    </div>
    <div class="bank flex-between">
      <div class="left">
        <p class="title">中国工商银行</p>
        <p class="text">尾号0732卡</p>
      </div>
      <van-icon name="arrow" class="arrow" />
    </div>
    <div class="price">
      <p class="text">提现金额 <span class="small">（可提：<span class="red">￥2800.00</span>）</span></p>
      <input type="text" placeholder="金额必须为100元以上">
    </div>
    <div class="button flex-center">申请提现</div>
  </div>
</template>

<script>
import { Icon } from 'vant';
export default {
  components: {
    'van-icon': Icon
  },
}
</script>

<style scoped lang="scss">
.withdrawDeposit {
  min-height: 100vh;
  background: #fff;
  box-sizing: border-box;
  padding: 35px;
  .way {
    .title {
      margin-bottom: 20px;
    }
    .item {
      width: 200px;
      height: 100px;
      border-radius: 14px;
      border: 2px solid #eee;
      font-size: 32px;
      img {
        width: 45px;
        margin-right: 14px;
      }
    }
    .active {
      background: url('src/assets/images/withdrawDeposit_chkcked.png') no-repeat center center;
      background-size: 100% 100%;
      border: 0;
      border-radius: 0;
    }
  }
  .cell {
    height: 110px;
    font-size: 32px;
    border-bottom: 1px solid #eee;
    .title {
      margin-right: 30px;
    }
  }
  .price {
    padding: 30px 0;
    border-bottom: 1px solid #2194e6;
    .text {
      color: #505050;
      margin-bottom: 40px;
      margin-top: 20px;
      .small {
        font-size: 26px;
        .red {
          color: #e51616;
        }
      }
    }
    input {
      font-size: 38px;
    }
  }
  .button {
    margin: 80px auto;
    background: linear-gradient(to bottom, #2194e6 , #21cce6);
    color: #fff;
    height: 90px;
    border-radius: 60px;
    font-size: 38px;
    box-shadow: 0 4px 16px #b3edf6;
  }
  .bank {
    height: 160px;
    border-bottom: 1px solid #eee;
    .title {
      font-size: 36px;
      margin-bottom: 10px;
    }
  }
}

</style>


