<template>
  <div class="appointment">
    <div class="info">
      <p class="name">华云生物科学体验馆</p>
      <p class="phone">电话：0755-25568628</p>
      <p class="address">地址：深圳市龙岗区坂田街道发达路云里智能园6栋2楼</p>
    </div>
    <div class="wrap">
      <div class="num flex-between">
        <p class="title">同行人数</p>
        <input type="number" placeholder="请输入人数">
      </div>
      <div class="time flex-between">
        <p class="title">预约时间</p>
        <p class="select flex-center-y">选择参观日期<van-icon name="arrow" class="arrow" /></p>
      </div>
      <div class="list flex-between">
        <div class="item" v-for="(item, index) in 6" :key="index" :class="index === 5 ? 'active' : ''" >
          <p class="am">上午</p>
          <p class="time_num">09:00~11:00</p>
          <p class="number">剩余28位</p>
        </div>
      </div>
    </div>
    <div class="button flex-center">提交</div>
  </div>
</template>

<script>
import { Icon } from 'vant';
export default {
  components: {
    'van-icon': Icon
  },
}
</script>

<style scoped lang="scss">
.appointment {
  background: url('src/assets/images/appointment_bg.png') no-repeat top;
  background-size: 100% auto;
  padding-top: 200px;
  .info {
    color: #fff;
    width: 81%;
    margin: 16px auto;
    p {
      margin-bottom: 8px;
      font-size: 24px
    }
    .name {
      font-size: 34px;
      margin-bottom: 12px;
    }
  }
  .wrap {
    width: 90%;
    margin: 0 auto;
    box-sizing: border-box;
    box-shadow: 0 4px 20px #e6e6e6;
    padding: 20px 40px;
    background: #fff;
    border-radius: 20px;
    font-size: 32px;
    .num {
      padding: 15px 0 35px 0;
      border-bottom: 1px solid #eee;
      input {
        text-align: right;
      }
    }
    .time {
      padding: 35px 0;
      .select {
        color: #2194e6;
      }
      .arrow {
        color: #bebebe;
        margin-left: 10px;
      }
    }
    .list {
      flex-wrap: wrap;
      .item {
        width: 30%;
        text-align: center;
        margin-bottom: 3.33%;
        border: 2px solid #eee;
        padding: 25px 0;
        border-radius: 14px;
        color: #7d7d7d;
        font-size: 28px;
        .time_num {
          margin: 18px 0;
          font-size: 26px;
          font-weight: bold;
          color: #333;
        }
      }
      .active {
        background: linear-gradient(to bottom, #2194e6 , #21cce6);
        color: #fff;
        .time_num {
          color: #fff;
        }
      }
    }
  }
  .button {
    width: 90%;
    margin: 30px auto;
    background: linear-gradient(to bottom, #2194e6 , #21cce6);
    color: #fff;
    height: 100px;
    border-radius: 60px;
    font-size: 38px;
    box-shadow: 0 4px 16px #b3edf6;
  }
}
</style>


