<template>
  <div class="myEvaluate">
    <van-tabs type="card" class="tab">
      <van-tab title="全部评价">
        <div class="list">
          <div class="item flex" v-for="(item, index) in 5" :key="index">
            <img src="../../assets/images/classify_item.png" alt="">
            <div class="info flex_1">
              <p class="title">基因检测3999套餐</p>
              <p class="price">￥3999.00</p>
              <router-link to="evaluate" class="button flex-center">去评价</router-link>
            </div>
          </div>
        </div>
      </van-tab>
      <van-tab title="待评价">内容 2</van-tab>
      <van-tab title="已评价">内容 2</van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { Tab, Tabs } from 'vant';
export default {
  components: {
    'van-tabs': Tabs,
    'van-tab': Tab
  },
}
</script>

<style scoped lang="scss">
.myEvaluate {
  margin-top: 30px;
}
.item {
  margin: 35px;
  background: #fff;
  padding: 25px;
  border-radius: 20px;
  img {
    width: 185px;
    height: 185px;
  }
  .info {
    margin-left: 25px;
    overflow: hidden;
    .title {
      font-size: 34px;
    }
    .price {
      margin: 20px 0;
      font-size: 36px;
      font-weight: bold;
      color: #dc2121;
    }
    .button {
      width: 180px;
      height: 55px;
      border: 2px solid #2198e6;
      border-radius: 30px;
      color: #2198e6;
    }
  }
}
</style>



