<template>
  <div>
    <div class="list">
      <div class="item flex-center-y">
        <div class="title">真实姓名：</div>
        <input type="text" placeholder="请输入真实姓名" v-model="data.realname">
      </div>
      <div class="item flex-center-y">
        <div class="title">身份证号码：</div>
        <input type="text" placeholder="请输入15/18位的身份证号码" v-model="data.cardnum">
      </div>
      <div class="button flex-center" @click="submit">提交</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: {
        realname: '',
        cardnum: ''
      }
    }
  },
  methods: {
    submit() {
      this.$post('user/RealnameAuthentication', this.data).then((res) => {
        Toast('保存成功')
        this.$router.go(-1)
      })
    }
  }
}
</script>

<style scoped lang="scss">
.list {
  padding: 30px;
  background: #fff;
  box-sizing: border-box;
  min-height: 100vh;
  .item {
    height: 100px;
    border-bottom: 1px solid #e9e9e9;
    .title {
      width: 180px;
    }
  }
}
.button {
  margin: 44px auto;
  background: linear-gradient(to bottom, #2194e6 , #21cce6);
  color: #fff;
  height: 90px;
  border-radius: 60px;
  font-size: 38px;
  box-shadow: 0 4px 16px #b3edf6;
}
</style>



