<template>
  <div>
    <div class="user flex-center-y">
      <img src="../../assets/images/columns9.png" alt="" class="address_icon">
      <div class="flex_1">
        <div class="flex-between">
          <p>收货人：林小七</p>
          <p>13531502899</p>
        </div>
        <p class="address">广东省深圳市龙华新区民治街道牛栏前大厦B1215</p>
      </div>
      <van-icon name="arrow" class="arrow" />
      <img src="../../assets/images/submitOrder_bg.png" alt="" class="submitOrder_bg">
    </div>
    <div class="list">
      <div class="info flex">
        <img src="../../assets/images/classify_item.png" alt="" class="product_img">
        <div class="flex_1">
          <div class="flex-between">
            <p class="title">基因检测3999套餐</p>
            <span class="price">￥3999.00</span>
          </div>
          <p class="text">肿瘤个体化化疗药物分子检测</p>
          <p class="num">x2</p>
        </div>
      </div>
      <div class="item flex-between">
        <div class="title">优惠券</div>
        <div class="right flex-center-y">
          <p>选择优惠劵</p>
          <van-icon name="arrow" class="arrow" />
        </div>
      </div>
      <div class="item flex-between">
        <div class="title">使用3840元宝抵扣3840元</div>
        <div class="right flex-center-y">
          <van-switch v-model="checked" />
        </div>
      </div>
      <div class="item flex-between">
        <div class="title">使用2600积分抵扣2600元</div>
        <div class="right flex-center-y">
          <van-switch v-model="checked" />
        </div>
      </div>
      <div class="item flex-between">
        <div class="title">备注</div>
        <input type="text" placeholder="请输入要备注的内容" class="flex_1">
      </div>
      <div class="price_info">
        <p class="flex-between">
          <span>商品金额：</span>
          <span class="price">￥7998.00</span>
        </p>
        <p class="flex-between">
          <span>运费：</span>
          <span class="price">+￥0.00</span>
        </p>
        <p class="flex-between">
          <span>优惠券抵扣：</span>
          <span class="price">-￥0.00</span>
        </p>
        <p class="flex-between">
          <span>元宝抵扣：</span>
          <span class="price">-￥3840.00</span>
        </p>
        <p class="flex-between">
          <span>积分抵扣：</span>
          <span class="price">-￥0.00</span>
        </p>
      </div>
    </div>
    <div class="button_wrap flex-between">
      <p>需支付：<span>￥4158.00</span></p>
      <div class="submit flex-center" @click="$router.push({path: '/success'})">提交订单</div>
    </div>
  </div>
</template>

<script>
import { Icon, Switch } from 'vant';
export default {
  components: {
    'van-icon': Icon,
    'van-switch': Switch
  },
  data() {
    return {
      checked: true
    }
  }
}
</script>

<style scoped lang="scss">
.user {
  padding: 30px 35px 35px 35px;
  background: #fff;
  position: relative;
  .address_icon {
    width: 50px;
  }
  .flex_1 {
    padding: 0 20px;
    overflow: hidden;
    .flex-between {
      font-size: 32px;
      margin-bottom: 12px;
      color: #000;
      font-weight: bold;
    }
    .address {
      line-height: 40px;
      color: #4c4c4c;
    }
  }
  .submitOrder_bg {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
  }
}
.list {
  padding: 0 35px;
  background: #fff;
  margin-top: 20px;
  margin-bottom: 100px;
  .info {
    padding: 30px 0;
    border-bottom: 1px solid #ebebeb;
    .product_img {
      width: 125px;
      height: 125px;
      border-radius: 14px;
    }
    .flex_1 {
      overflow: hidden;
      padding-left: 20px;
    }
    .title {
      font-weight: bold;
      font-size: 32px;
    }
    .price {
      color: #dc2121;
      font-weight: bold;
      font-size: 32px;
    }
    .text {
      margin: 8px 0;
    }
    .num {
      text-align: right;
      color: #333;
    }
  }
  .item {
    height: 100px;
    font-size: 32px;
    border-bottom: 1px solid #ebebeb;
    input {
      text-align: right;
      padding-left: 50px;
    }
    .title {
      color: #000;
    }
    .right {
      font-size: 28px;
      color: #4e4e4e;
    }
  }
  .price_info {
    overflow: hidden;
    color: #020202;
    p {
      margin: 35px 0;
      .price {
        color: #ef1818;
        font-weight: bold;
      }
    }
  }
}
.button_wrap {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100px;
  background: #fff;
  box-shadow: 0 0 10px #eee;
  p {
    margin-left: 30px;
    color: #000;
    span {
      color: #ef1818;
      font-weight: bold;
      font-size: 36px;
    }
  }
  .submit {
    width: 270px;
    height: 100px;
    background: linear-gradient(to right, #21cce6 , #2194e6);
    color: #fff;
    font-size: 36px;
  }
}
</style>



