<template>
  <div class="cellularState">
    <div class="exhibition" v-if="result">
      <div class="title">
        <h1>细胞储存状态查询</h1>
        <p>细胞状态随时查</p>
      </div>
      <div class="warp">
        <div class="item flex-between">
          <p>细胞编号<span>*</span></p>
          <input type="text" placeholder="16位细胞编号">
        </div>
      </div>
      <div class="button flex-center" @click="result=false">提交</div>
      <div class="introduction">
        <h3>下单须知</h3>
        <p class="text">1.目前报告查询服务仅向华云平台会员用户提供。</p>
        <p class="text">2.体检报告一般在体检结束后5-10个工作日之后即可在网上查询，具体以各体检中心信息为准。</p>
        <p class="text">3.不支持在线查询体检报告或需要纸质报告，请您直接与体检中心联系。</p>
        <p class="text">4.如不支持在线查询体检报告的用户，请您直接与体检中心联系。</p>
      </div>
    </div>

    <div class="result_wrap" v-else>
      <div class="rotate">
        <img src="../../assets/images/rotate_wrap.png" alt="" class="rotate_wrap">
        <div class="rotate_con2">
          <img src="../../assets/images/rotate_con.png" alt="" class="rotate_con">
        </div>
      </div>
      <div class="dayNmber">
        <p class="day"><span>4</span>年<span>08</span>月<span>23</span>天</p>
        <p class="text">细胞储存天数</p>
      </div>
      <div class="info">
        <h3>细胞信息</h3>
        <div class="item flex-center-y flex_1">
          <div class="q">姓名：</div>
          <div class="text">林晓琪</div>
        </div>
        <div class="item flex-center-y flex_1">
          <div class="q">性别：</div>
          <div class="text">男</div>
        </div>
        <div class="item flex-center-y flex_1">
          <div class="q">储存日期：</div>
          <div class="text">2014年08月22日</div>
        </div>
        <div class="item flex-center-y flex_1">
          <div class="q">储存期限：</div>
          <div class="text">20年</div>
        </div>
        <div class="item flex-center-y flex_1">
          <div class="q">细胞储存编码：</div>
          <div class="text">201408225</div>
        </div>
        <div class="item flex-center-y flex_1">
          <div class="q">保单编号：</div>
          <div class="text">6481659475</div>
        </div>
        <div class="item flex-center-y flex_1">
          <div class="q">合同编号：</div>
          <div class="text">5４812548</div>
        </div>
        <div class="item flex-center-y flex_1">
          <div class="q">备注：</div>
          <div class="text">无</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      result: true
    }
  }
}
</script>


<style scoped lang="scss">
.cellularState {
  .exhibition{
    background: url('src/assets/images/user_bg.png') no-repeat top;
    background-size: 100% auto;
  }
  .result_wrap {
    background: url('src/assets/images/cellularState_bg.png') no-repeat top;
    background-size: 100% auto;
  }
  .title {
    color: #fff;
    text-align: center;
    padding: 70px 0;
    h1 {
      font-size: 50px;
      margin-bottom: 20px;
      font-weight: 500;
    }
    p {
      font-size: 32px;
    }
  }
  .warp {
    padding: 0 40px 30px 40px;
    background: #fff;
    width: 90%;
    box-sizing: border-box;
    margin: 0 auto 30px auto;
    border-radius: 20px;
    box-shadow: 0 4px 20px #e6e6e6;
    .item {
      height: 110px;
      border-bottom: 1px solid #eee;
      font-size: 32px;
      p {
        white-space: nowrap;
        span {
          color: #e62121;
        }
      }
      input {
        flex: 1;
        overflow: hidden;
        padding: 0 10px;
        text-align: right;
      }
    }
  }
  .button {
    width: 90%;
    margin: 44px auto;
    background: linear-gradient(to bottom, #2194e6 , #21cce6);
    color: #fff;
    height: 90px;
    border-radius: 60px;
    font-size: 38px;
    box-shadow: 0 4px 16px #b3edf6;
  }
  .introduction {
    width: 90%;
    padding: 30px;
    box-sizing: border-box;
    background: #fafafa;
    margin: 180px auto 0 auto;
    h3 {
      font-size: 36px;
      background: url('src/assets/images/h3_bg.png') no-repeat bottom left;
      background-size: 80px 16px;
      padding: 4px 0;
      margin-bottom: 25px;
    }
    .text {
      line-height: 44px;
      margin-bottom: 6px;
      color: #5b5b5b;
    }
  }
  .result_wrap {
    overflow: hidden;
    .rotate {
      position: relative;
      text-align: center;
      margin: 80px 0;
      .rotate_wrap {
        width: 550px;
        animation: rotate_wrap 20s linear infinite;
      }
      .rotate_con {
        animation: rotate_con 30s linear infinite;
      }
      .rotate_con2 {
        position: absolute;
        width: 5.7rem;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        img {
          width: 100%;
        }
      }
    }
    .dayNmber {
      color: #fff;
      text-align: center;
      font-size: 32px;
      .day {
        font-size: 36px;
        margin-bottom: 14px;
        span {
          font-size: 60px;
          margin: 0 10px;
          color: #f6f93a;
        }
      }
    }
    .info {
      margin-top: 140px;
      padding: 0 50px;
      font-size: 26px;
      h3 {
        text-align: center;
        font-size: 34px;
        margin-bottom: 30px;
      }
      .item {
        padding: 10px;
        overflow: hidden;
        .q {
          white-space: nowrap;
          color: #818181;
        }
      }
    }
  }
}
@keyframes rotate_wrap {
  0%{-webkit-transform:rotate(0deg);}

  25%{-webkit-transform:rotate(90deg);}

  50%{-webkit-transform:rotate(180deg);}

  75%{-webkit-transform:rotate(270deg);}

  100%{-webkit-transform:rotate(360deg);}

}
@keyframes rotate_con {
  0%{-webkit-transform:rotate(-0deg);}

  25%{-webkit-transform:rotate(-90deg);}

  50%{-webkit-transform:rotate(-180deg);}

  75%{-webkit-transform:rotate(-270deg);}

  100%{-webkit-transform:rotate(-360deg);}
}
</style>



