<template>
  <div class="classify">
    <div class="search">
      <img src="../../assets/images/sousuo.png" alt="">
      <input type="text" placeholder="请输入搜索关键词" @focus="$router.push({path: '/search'})">
    </div>
    <div class="con flex">
      <ul class="left">
        <li :class="active === index ? 'active' : ''" @click="getData(index)" v-for="(item, index) in tabList" :key="index">{{item.title}}</li>
      </ul>
      <div class="right">
        <div class="list">
          <img src="../../assets/images/classify_banner.png" alt="" class="banner">
          <router-link to="/details" class="item flex-center-y" v-for="(item, index) in 8" :key="index">
            <img src="../../assets/images/classify_item.png" alt="">
            <div class="info flex_1">
              <p class="title ellipsis">基因检测3999套餐</p>
              <p class="text ellipsis">肿瘤个体化化疗药物分子检测</p>
              <p class="price">￥1330.00</p>
            </div>
            <van-icon name="arrow" class="arrow" />
          </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant';
export default {
  components: {
    'van-icon': Icon
  },
  data() {
    return {
      tabList: [
        {
          title: '基因工程',
          loading: false,
          page: 1,
          list: []
        },
        {
          title: '细胞工程',
          loading: false,
          page: 1,
          list: []
        },
        {
          title: '抗衰保健',
          loading: false,
          page: 1,
          list: []
        },
        {
          title: '慢病管理',
          loading: false,
          page: 1,
          list: []
        },
      ],
      active: 0
    }
  },
  created() {

  },
  methods: {
    getData(index) {
      this.active = index
    }
  }
}
</script>

<style scoped lang="scss">
.classify {
  height: 100vh;
  box-sizing: border-box;
  padding-top: 130px;
  padding-bottom: 110px;
}
.search {
  padding: 25px;
  background: #fff;
  position: fixed;
  border-bottom: 1px solid #eee;
  box-sizing: border-box;
  top: 0;
  width: 100%;
  left: 0;
  input {
    width: 100%;
    height: 80px;
    background: #f2f2f2;
    border-radius: 60px;
    box-sizing: border-box;
    padding-left: 90px;
    padding-right: 20px;
  }
  img {
    position: absolute;
    left: 55px;
    width: 44px;
    top: 42px;
  }
}
.con {
  height: 100%;
  overflow: hidden;
  .left {
    min-height: 100%;
    width: 190px;
    background: #f7f7f7;
    font-size: 32px;
    border-right: 2px solid #eee;
    overflow-y: auto;
    overflow-x: hidden;
    li {
      cursor: pointer;
      height: 90px;
      line-height: 90px;
      text-align: center;
      border-bottom: 2px solid #eee;
      &.active {
        color: #2277e2;
        border-left: 6px solid #2277e2;
        background: #fff;
        border-bottom: 0;
        position: relative;
        left: 1px;
        top: 1px;
      }
    }
  }
  .right {
    min-height: 100%;
    flex: 1;
    background: #fff;
    padding: 20px 25px 0 25px;
    overflow-x: hidden;
    .banner {
      width: 100%;
    }
    .list {
      padding-bottom: 30px;
    }
    .item {
      padding: 25px 0;
      border-bottom: 1px solid #e5e5e5;
      img {
        width: 130px;
        height: 130px;
      }
      .info {
        padding: 0 20px;
        overflow: hidden;
        .title {
          color: #454545;
          font-size: 32px;
        }
        .text {
          color: #454545;
          font-size: 28px;
          margin: 6px 0;
        }
        .price {
          color: #dc2121;
        }
      }
      .arrow {
        font-size: 40px;
        color: #c6c6c6;
      }
    }
  }
}
</style>


