<template>
  <div class="myOrder">
    <van-tabs type="card" class="tab" v-model="active" @change="getData" swipeable>
      <van-tab v-for="(item, index) in tabList" :title="item.title + '(253)'" :key="index">
        <van-list v-model="item.loading" :finished="item.finished" finished-text="没有更多了" @load="getData" >
          <div class="list">
            <div class="item flex-center-y" v-for="(item, index) in item.list" :key="index">
              <div class="img">
                <img src="../../assets/images/classify_item.png" alt="">
              </div>
              <div class="info flex flex_1">
                <div class="flex_1">
                  <p class="ellipsis">昵称：等你下课</p>
                  <p class="ellipsis">级别：一级</p>
                </div>
                <div class="flex_1">
                  <p class="ellipsis">地区：广东深圳</p>
                  <p class="ellipsis">时间：2019-02-20</p>
                </div>
              </div>
            </div>
          </div>
        </van-list>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { Tab, Tabs, List } from 'vant';
export default {
  components: {
    'van-tabs': Tabs,
    'van-tab': Tab,
    'van-list': List
  },
  data() {
    return {
      active: 0,
      tabList: [
        {
          title: '所有',
          loading: false,
          page: 1,
          finished: false,
          list: []
        },
        {
          title: '一级',
          loading: false,
          page: 1,
          finished: false,
          list: []
        },
        {
          title: '二级',
          loading: false,
          page: 1,
          finished: false,
          list: []
        }
      ]
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      let index = this.active
      if (this.tabList[index].finished) return false
      this.tabList[index].loading = true
      let data = {
        p: this.tabList[index].page,
        level: this.active
      }
      this.$post('order/order_list', data).then((res) => {
        this.tabList[index].list = this.tabList[index].list.concat(res.data)
        this.tabList[index].loading = false
        if (res.data.length < 10)  this.tabList[index].finished = true
        this.tabList[index].page = ++this.tabList[index].page
      })
    }
  }
}
</script>

<style scoped lang="scss">
.myOrder {
  padding-top: 20px;
}
.list {
  margin: 20px 30px;
  .item {
    background: #fff;
    padding: 20px 25px;
    border-radius: 30px;
    margin-top: 30px;
    img {
      border-radius: 50%;
      width: 130px;
      height: 130px;
    }
    .info {
      padding-left: 20px;
      overflow: hidden;
      .flex_1 {
        overflow: hidden;
      }
      p {
        &:first-child {
          margin-bottom: 25px;
        }
      }
    }
  }
}
</style>



