<template>
  <div class="signIn">
    <div class="rule flex-center">签到规则</div>
    <div class="con">
      <p class="title">已连续签到<span> 3 </span>天</p>
      <p class="text">连续签到7天可获得额外奖励</p>
      <div class="list flex-between">
        <div class="item flex-center" v-for="(item, index) in 7" :key="index" :class="index < 2 ? 'success' : ''">
          <p class="num">{{++index}}</p>
          <img src="../../assets/images/signIn_icon1.png" alt="" v-if="index <= 2" style="height: 10px;">
          <img src="../../assets/images/signIn_icon2.png" alt="" v-if="index > 2 && index !==7">
          <img src="../../assets/images/signIn_icon3.png" alt="" v-if="index === 7">
        </div>
      </div>
      <p class="title">今天还没签到，赶快签到吧！</p>
      <div class="button flex-center" @click="signIn">马上签到</div>
      <p class="total">累计获得<span> 25487 </span>积分</p>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
     
    }
  },
  methods: {
    signIn() {
      this.$post('user/user_sign', {}).then((res) => {
        // Toast('保存成功')
        // this.$router.go(-1)
      })
    }
  }
}
</script>

<style scoped lang="scss">
.signIn {
  background: url('src/assets/images/signIn_bg.png') no-repeat top;
  background-size: 100% 100%;
  min-height: 100vh;
  position: relative;
  .rule {
    position: absolute;
    right: 0;
    width: 150px;
    height: 50px;
    color: #fff;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    background: linear-gradient(to right, #ffc000 , #eb6e1a);
    top: 3%;
  }
  .con {
    background: #fff;
    width: 90%;
    box-sizing: border-box;
    padding: 40px;
    position: absolute;
    top: 46%;
    left: 50%;
    transform: translateX(-50%);
    border-radius: 30px;
    .title {
      text-align: center;
      color: #454545;
      font-size: 36px;
      margin-bottom: 20px;
      span {
        color: #2194e6;
        font-weight: bold
      }
    }
    .text {
      color: #82858a;
      text-align: center;
      font-size: 28px;
    }
    .list {
      margin: 40px 0;
      .item {
        width: 70px;
        height: 90px;
        background: #eceef3;
        border-radius: 14px;
        flex-direction: column;
        font-size: 32px;
        font-weight: bold;
        color: #454545;
        img {
          height: 30px;
          margin-top: 4px;
        }
      }
      .success {
        background: linear-gradient(to bottom, #2194e6 , #21cce6);
        color: #fff;
      }
    }
    .button {
      width: 340px;
      background: linear-gradient(to bottom, #2194e6 , #21cce6);
      color: #fff;
      height: 90px;
      border-radius: 60px;
      font-size: 38px;
      margin: 46px auto;
      box-shadow: 0 4px 16px #b3edf6;
    }
    .total {
      text-align: center;
      font-size: 30px;
      span {
        color: #ec7019;
        font-weight: bold;
      }
    }
  }
}
</style>



