<template>
  <div>
    <div class="list">
      <div class="item flex-between" v-for="(item, index) in 6" :key="index">
        <div class="flex-center-y">
          <img src="../../assets/images/help_icon.png" alt="">
          <p class="ellipsis">注册与账户</p>
        </div>
        <van-icon name="arrow" class="arrow" />
      </div>
    </div>
    <div class="button flex-center-y">
      <div class="flex-center flex_1"><img src="../../assets/images/help1.png" alt="">联系客服</div>
      <div class="flex-center flex_1" @click="$router.push({path: '/feedback'})"><img src="../../assets/images/help2.png" alt="">意见反馈</div>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant';
export default {
  components: {
    'van-icon': Icon
  },
}
</script>

<style scoped lang="scss">
.list {
  padding: 0 30px;
  background: #fff;
  margin-bottom: 100px;
  .item {
    overflow: hidden;
    height: 100px;
    border-bottom: 1px solid #f2f2f2;
    font-size: 32px;
    img {
      width: 44px;
      margin-right: 15px;
    }
    .flex-center-y {
      overflow: hidden;
    }
  }
}
.button {
  position: fixed;
  bottom: 0;
  width: 100%;
  left: 0;
  height: 100px;
  background: #fff;
  font-size: 34px;
  img {
    width: 40px;
    margin-right: 10px;
  }
  div:first-child {
    border-right: 1px solid #e5e5e5;
  }
}
</style>



