<template>
  <div class="evaluate">
    <div class="header flex-center-y">
      <img src="../../assets/images/classify_item.png" alt="">
      <div class="rate flex_1">
        <p class="title">评分</p>
        <van-rate v-model="data.goods_rank" color="#21b2e6" />
      </div>
    </div>
    <div class="con">
      <textarea name="" id="" cols="30" rows="10" placeholder="赶快分享你的使用体验吧~" v-model="data.content"></textarea>
      <div class="flex-wrap">
        <div class="img_wrap">
          <img src="../../assets/images/1007.png" alt="" class="img">
          <img src="../../assets/images/img_del.png" alt="" class="close">
        </div>
        <div class="img_wrap">
          <img src="../../assets/images/1007.png" alt="" class="img">
          <img src="../../assets/images/img_del.png" alt="" class="close">
        </div>
        <van-uploader :after-read="onReads" accept="image/*"  multiple v-if="img.length < 5">
          <img src="../../assets/images/upload.png" alt="" class="upload img">
        </van-uploader>
      </div>
    </div>
    <div class="button flex-center">提交</div>
  </div>
</template>

<script>
import { Icon, Rate, Uploader } from 'vant';
export default {
  components: {
    'van-icon': Icon,
    'van-uploader': Uploader,
    'van-rate': Rate
  },
  data() {
    return {
      value: 2,
      img: [],
      img_list: [],
      data: {
        goods_rank: 5,
        content: '',
        rec_id: ''
      }
    }
  },
  methods: {
    onReads(file) {
      
    }
  }
}
</script>

<style scoped lang="scss">
.evaluate {
  padding: 0 35px;
  min-height: 100vh;
  background: #fff;
  .header {
    padding: 30px 0;
    border-bottom: 1px solid #eee;
    img {
      width: 160px;
      height: 160px;
      border-radius: 14px;
    }
    .rate {
      padding-left: 30px;
      .title {
        font-size: 32px;
        margin-bottom: 15px;
        margin-left: 10px;
      }
    }
  }
  .con {
    textarea {
      border: none;
      resize: none;
      width: 100%;
      font-size: 30px;
      padding: 30px 15px;
      height: 250px;
      box-sizing: border-box;
    }
    .img_wrap {
      position: relative;
      .close {
        position: absolute;
        right: 10px;
        top: -10px;
        width: 30px
      }
    }
    .img {
      width: 160px;
      height: 160px;
      margin-right: 20px;
      margin-bottom: 20px;
      border-radius: 14px;
    }
    .upload {
      border-radius: 0;
    }
  }
  .button {
    margin: 44px auto;
    background: linear-gradient(to bottom, #2194e6 , #21cce6);
    color: #fff;
    height: 90px;
    border-radius: 60px;
    font-size: 38px;
    box-shadow: 0 4px 16px #b3edf6;
  }
}
</style>



