<template>
  <div>
    <router-view/>
    <div class="tabbar_wrap">
      <van-tabbar v-model="active" :z-index="100" class="tabbar">
        <van-tabbar-item to="/home">
          <span>首页</span>
          <img slot="icon" slot-scope="props" :src="props.active ? icon.active1 : icon.icon1">
        </van-tabbar-item>
        <van-tabbar-item  to="/classify">
          <span>分类</span>
          <img slot="icon" slot-scope="props" :src="props.active ? icon.active2 : icon.icon2">
        </van-tabbar-item>
        <van-tabbar-item to="/cart">
          <span>购物车</span>
          <img slot="icon" slot-scope="props" :src="props.active ? icon.active3 : icon.icon3">
        </van-tabbar-item>
        <van-tabbar-item to="/user">
          <span>我的</span>
          <img slot="icon" slot-scope="props" :src="props.active ? icon.active4 : icon.icon4">
        </van-tabbar-item>
      </van-tabbar>
    </div>
  </div>
</template>

<script>
import { Tabbar, TabbarItem } from 'vant';
import icon1 from '../assets/images/icon1.png'
import icon2 from '../assets/images/icon2.png'
import icon3 from '../assets/images/icon3.png'
import icon4 from '../assets/images/icon4.png'
import active1 from '../assets/images/icon_active1.png'
import active2 from '../assets/images/icon_active2.png'
import active3 from '../assets/images/icon_active3.png'
import active4 from '../assets/images/icon_active4.png'
export default {
  components: {
    'van-tabbar': Tabbar,
    'van-tabbar-item': TabbarItem
  },
  data() {
    return {
      active: 0,
      icon: {
        icon1: icon1,
        icon2: icon2,
        icon3: icon3,
        icon4: icon4,
        active1: active1,
        active2: active2,
        active3: active3,
        active4: active4
      }
    }
  },
  created () {
    this.Change(this.$route.name)
  },
  methods: {
    Change(name) {
      if (name === 'home') this.active = 0
      else if (name === 'classify') this.active = 1
      else if (name === 'cart') this.active = 2
      else if (name === 'user') this.active = 3
      else this.active = 0
    }
  },
  watch: {
    $route(to,from) {
      this.Change(to.name)
    }
  }
}
</script>
<style scoped lang="scss">

</style>




