<template>
  <div class="myOrder">
    <div class="header flex-center-y">
      <div class="item flex_1">
        <p class="num">2480.00</p>
        <p class="text">今日获得元宝</p>
      </div>
      <div class="link"></div>
      <div class="item flex_1">
        <p class="num">542000.00</p>
        <p class="text">团队订单总额</p>
      </div>
    </div>
    <van-tabs type="card" class="tab">
      <van-tab v-for="(item, index) in tabList" :title="item.title + '(253)'" :key="index">
        <!-- <van-list v-model="item.loading" :finished="item.finished" finished-text="没有更多了" @load="getData" > -->
          <div class="list">
            <div class="wrap"  v-for="(item, index) in 5" :key="index">
              <div class="head flex-between">
                <div class="left flex">
                  <p>订单日期: 2018-10-10</p>
                  <p>完成日期: 2018-08-30</p>
                </div>
                <div class="stats await flex-center" v-if="index===0">待付款</div>
                <div class="stats account flex-center" v-if="index===1">已付款</div>
                <div class="stats completed flex-center" v-if="index===2">已完成</div>
              </div>
              <div class="item flex-center-y">
                <div class="img">
                  <img src="../../assets/images/classify_item.png" alt="">
                </div>
                <div class="info flex flex_1">
                  <div class="flex_1">
                    <p class="ellipsis">昵称：等你下课</p>
                    <p class="ellipsis">金额：<span>￥399.00</span></p>
                  </div>
                  <div class="flex_1">
                    <p class="ellipsis">级别：一级</p>
                    <p class="ellipsis">元宝：<span>￥7.00</span></p>
                  </div>
                </div>
              </div>
            </div>
          </div>
         <!-- </van-list> -->
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { Tab, Tabs, List } from 'vant';
export default {
  components: {
    'van-tabs': Tabs,
    'van-tab': Tab,
    'van-list': List
  },
   data() {
    return {
      active: 0,
      tabList: [
        {
          title: '所有',
          loading: false,
          page: 1,
          finished: false,
          list: []
        },
        {
          title: '一级',
          loading: false,
          page: 1,
          finished: false,
          list: []
        },
        {
          title: '二级',
          loading: false,
          page: 1,
          finished: false,
          list: []
        }
      ]
    }
  },
  created() {
    // this.getData()
  },
  methods: {
    getData() {
      let index = this.active
      if (this.tabList[index].finished) return false
      this.tabList[index].loading = true
      let data = {
        p: this.tabList[index].page,
        level: this.active
      }
      this.$post('', data).then((res) => {
        this.tabList[index].list = this.tabList[index].list.concat(res.data)
        this.tabList[index].loading = false
        if (res.data.length < 10)  this.tabList[index].finished = true
        this.tabList[index].page = ++this.tabList[index].page
      })
    }
  }
}
</script>

<style scoped lang="scss">
.header {
  height: 160px;
  margin-bottom: 25px;
  background: linear-gradient(to right, #21cce6 , #2194e6);
  .link {
    width: 2px;
    height: 50px;
    background: #fff;
  }
  .head {
    height: 90px;
  }
  .item {
    color: #fff;
    text-align: center;
    .num {
      font-size: 40px;
      margin-bottom: 4px;
    }
    .text {
      font-size: 20px;
    }
  }
}
.wrap {
  background: #fff;
  margin-top: 30px;
  .head {
    height: 90px;
    border-bottom: 1px solid #eee;
    padding: 0 20px;
    font-size: 22px;
    .left {
      p:first-child {
        margin-right: 10px;
      }
    }
    .stats {
      width: 110px;
      height: 40px;
      border-radius: 30px;
      font-size: 16px
    }
    .await {
      border: 2px solid #ecae33;
      color: #ecae33;
    }
    .account {
      background: #ecae33;
      border: 2px solid #ecae33;
      color: #fff;
    }
    .completed {
      background: #2194e6;
      border: 2px solid #2194e6;
      color: #fff;
    }
  }
}
.list {
  margin: 20px 30px;
  .item {
    background: #fff;
    padding: 20px 25px;
    border-radius: 30px;
    img {
      border-radius: 50%;
      width: 130px;
      height: 130px;
    }
    .info {
      padding-left: 20px;
      overflow: hidden;
      .flex_1 {
        overflow: hidden;
      }
      p {
        &:first-child {
          margin-bottom: 25px;
        }
        span {
          color: #ecae33
        }
      }
    }
  }
}
</style>



