<template>
  <div class="cellularState">
    <div class="exhibition" v-if="result">
      <div class="title">
        <h1>体检记录查询</h1>
        <p>体检报告随时查</p>
      </div>
      <div class="warp">
        <div class="item flex-between">
          <p>体检编号<span>*</span></p>
          <input type="text" placeholder="16位细胞编号">
        </div>
      </div>
      <div class="button flex-center" @click="result=false">提交</div>
      <div class="introduction">
        <h3>下单须知</h3>
        <p class="text">1.目前报告查询服务仅向华云平台会员用户提供。</p>
        <p class="text">2.体检报告一般在体检结束后5-10个工作日之后即可在网上查询，具体以各体检中心信息为准。</p>
        <p class="text">3.不支持在线查询体检报告或需要纸质报告，请您直接与体检中心联系。</p>
        <p class="text">4.如不支持在线查询体检报告的用户，请您直接与体检中心联系。</p>
      </div>
    </div>
    <div class="result_wrap" v-else>
      <div class="title">
        <h1>体检记录查询</h1>
        <p>体检报告随时查</p>
      </div>
      <div class="result">
        <img src="../../assets/images/result.png" alt="">
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      result: true
    }
  }
}
</script>

<style scoped lang="scss">
.cellularState {
  background: url('src/assets/images/user_bg.png') no-repeat top;
  background-size: 100% auto;
  .title {
    color: #fff;
    text-align: center;
    padding: 70px 0;
    h1 {
      font-size: 50px;
      margin-bottom: 20px;
      font-weight: 500;
    }
    p {
      font-size: 32px;
    }
  }
  .warp {
    padding: 0 40px 30px 40px;
    background: #fff;
    width: 90%;
    box-sizing: border-box;
    margin: 0 auto 30px auto;
    border-radius: 20px;
    box-shadow: 0 4px 20px #e6e6e6;
    .item {
      height: 110px;
      border-bottom: 1px solid #eee;
      font-size: 32px;
      p {
        white-space: nowrap;
        span {
          color: #e62121;
        }
      }
      input {
        flex: 1;
        overflow: hidden;
        padding: 0 10px;
        text-align: right;
      }
    }
  }
  .button {
    width: 90%;
    margin: 44px auto;
    background: linear-gradient(to bottom, #2194e6 , #21cce6);
    color: #fff;
    height: 90px;
    border-radius: 60px;
    font-size: 38px;
    box-shadow: 0 4px 16px #b3edf6;
  }
  .introduction {
    width: 90%;
    padding: 30px;
    box-sizing: border-box;
    background: #fafafa;
    margin: 180px auto 0 auto;
    h3 {
      font-size: 36px;
      background: url('src/assets/images/h3_bg.png') no-repeat bottom left;
      background-size: 80px 16px;
      padding: 4px 0;
      margin-bottom: 25px;
    }
    .text {
      line-height: 44px;
      margin-bottom: 6px;
      color: #5b5b5b;
    }
  }
  .result {
    width: 90%;
    box-sizing: border-box;
    margin: 0 auto 30px auto;
    border-radius: 20px;
    box-shadow: 0 4px 20px #e6e6e6;
    background: #fff;
    img {
      width: 100%;
    }
  }
}
</style>



