<template>
  <div class="orderDetails">
    <div class="head">
      <div class="title">{{data.order_status_detail}}</div>
      <div class="text">卖家已发货，请准备收货</div>
    </div>
    <div class="logistics">
      <div class="item">
        <p class="title">物流信息:{{data.shipping_name}}  {{data.shipping_code}}</p>
        <p class="time">2019-03-02 19:14:17</p>
      </div>
      <div class="item">
        <p class="title">收货人:{{data.consignee}}  {{data.mobile}}</p>
        <p class="address">{{data.province}}{{data.city}}{{data.district}}{{data.address}}</p>
      </div>
    </div>
    <div class="wrap">
      <div class="product flex">
        <img :src="data.order_goods[0].original_img" alt="">
        <div class="info flex_1">
          <p class="title">{{data.order_goods[0].goods_name}}</p>
          <p class="price">￥{{data.order_goods[0].goods_price}} <span>X{{data.order_goods[0].goods_num}}</span></p>
          <div class="afterSale flex">
            <p class="button flex-center" @click="$router.push({path: '/afterSale'})">申请售后</p>
          </div>
        </div>
      </div>
      <div class="list">
        <h4>订单信息</h4>
        <div class="item flex-between">
          <div class="title">订单号码：</div>
          <div class="text">{{data.order_sn}}</div>
        </div>
        <div class="item flex-between">
          <div class="title">下单时间：</div>
          <div class="text">{{data.pay_time | date}}</div>
        </div>
        <div class="item flex-between">
          <div class="title">支付方式：</div>
          <div class="text">{{data.pay_name}}</div>
        </div>
        <div class="item flex-between">
          <div class="title">备注：</div>
          <div class="text" style="color: #868686">无</div>
        </div>
      </div>
      <div class="list">
        <div class="item flex-between">
          <div class="title">商品金额：</div>
          <div class="text">￥7998.00</div>
        </div>
        <div class="item flex-between">
          <div class="title">运费：</div>
          <div class="text">+￥0.00</div>
        </div>
        <div class="item flex-between">
          <div class="title">优惠券抵扣：</div>
          <div class="text">-￥0.00</div>
        </div>
        <div class="item flex-between">
          <div class="title">元宝抵扣：</div>
          <div class="text">-￥3840.00</div>
        </div>
        <div class="item flex-between">
          <div class="title">积分抵扣：</div>
          <div class="text">-￥0.00</div>
        </div>
        <div class="item flex-between">
          <div class="title">实付金额：</div>
          <div class="text" style="color: #e22424">￥4158.00</div>
        </div>
      </div>
    </div>
    <div class="handle flex-center-y">
      <div class="border flex-center">联系客服</div>
      <!-- <div class="border flex-center">取消订单</div> -->
      <!-- <div class="background flex-center">付款</div> -->
      <div class="background_blue flex-center">确认收货</div>
    </div>
  </div>
</template>

<script>
import util from '../utils/utils.js'
export default {
  created() {
    this.getData()
  },
  data() {
    return {
      data: {
        order_goods: [{}]
      }
    }
  },
  methods: {
    getData() {
      this.$post('order/order_detail', {id: this.$route.query.id}).then((res) => {
        this.data = res.data
      })
    }
  },
  filters: {
    date(val) {
      if (val) {
        let date = util.format(val*1000)
        return date
      }
    }
  }
}
</script>

<style scoped lang="scss">
.orderDetails {
  background: url('src/assets/images/orderDetails_bg.png') no-repeat top;
  background-size: 100% auto;
  overflow: hidden;
  padding: 0 35px;
  background-color: #fff;
}
.head {
  padding: 40px 0;
  color: #fff;
  .title {
    font-size: 46px;
    margin-bottom: 14px;
  }
}
.logistics {
  padding: 0 30px;
  box-shadow: 0 4px 20px #e6e6e6;
  background: #fff;
  border-radius: 14px;
  margin-bottom: 30px;
  .item {
    padding: 30px 0;
    &:first-child {
      border-bottom: 1px solid #e1e1e1;
    }
    .time {
      margin-top: 15px;
      color: #848484;
      font-size: 26px;
    }
    .address {
      margin-top: 15px;
      color: #4c4c4c;
      font-size: 26px;
    }
  }
}
.wrap {
  background: #fff;
  margin-bottom: 100px;
  .product {
    padding: 25px 0;
    border-bottom: 1px solid #eee;
    img {
      width: 120px;
      height: 120px;
      border-radius: 10px;
    }
    .info {
      overflow: hidden;
      padding-left: 20px;
      .title {
        font-size: 30px;
        margin-bottom: 8px;
      }
      span {
        color: #848484;
      }
      .afterSale {
        justify-content: flex-end;
        .button {
          width: 150px;
          height: 60px;
          border-radius: 60px;
          border: 2px solid #eee;
          color: #848484;
        }
      }
    }
  }
  .list {
    padding: 40px 0;
    border-bottom: 1px solid #eee;
    h4 {
      font-size: 36px;
      font-weight: 100;
      margin-bottom: 30px;
      color: #333;
    }
    .item {
      margin-bottom: 20px;
      &:last-child {
        margin-bottom: 0;
      }
      .title {
        color: #868686;
      }
      .text {
        color: #020202;
      }
    }
  }
}
.handle {
  height: 100px;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  background: #fff;
  box-shadow: 0 0 10px #ece8e8;
  justify-content: flex-end;
  div {
      width: 170px;
      height: 50px;
      border-radius: 30px;
      margin-right: 20px;
      font-size: 28px;
      // &:last-child {
      //   margin-right: 0;
      // }
    }
    .border {
      border: 2px solid #efefef;
      box-sizing: border-box;
      color: #545454;
    }
    .background {
      background: linear-gradient(to right, #fcd54e , #edb342);
      color: #fff;
    }
    .background_blue  {
      background: linear-gradient(to right, #21cce6 , #2697e7);
      color: #fff;
    }
}
</style>




