<template>
  <div class="container user">
    <div class="head">
      <div class="info flex-center-y">
        <router-link to="user_info" class="portrait flex-center">
          <img src="../../assets/images/user_bg.png" alt="">
        </router-link>
        <div class="order">
          <div class="flex-center-y">
            <p class="name">林小七</p>
            <p class="identity">初级会员</p>
          </div>
          <p class="id">ID:1213</p>
        </div>
      </div>
      <router-link to="settings">
          <img src="../../assets/images/setup.png" alt="" class="setup">
      </router-link>
      <router-link to="signIn" class="signIn flex-between">
        <img src="../../assets/images/signIn.png" alt="">
        <p>每日签到</p>
        <van-icon name="arrow" class="arrow" />
      </router-link>
    </div>
    <div class="block flex">
      <div class="wallet flex_1 flex-center-y">
        <img src="../../assets/images/wallet1.png" alt="">
        <router-link to="myGold" class="text">
          <p class="num">3824.00</p>
          <p class="txt">我的元宝</p>
        </router-link>
      </div>
      <div class="wallet flex_1 flex-center-y">
        <img src="../../assets/images/wallet2.png" alt="">
        <router-link to="myIntegral"  class="text">
          <p class="num">3824</p>
          <p class="txt">我的积分</p>
        </router-link>
      </div>
    </div>
    <div class="block flex column1">
      <router-link to="myTeam" class="item">
        <img src="../../assets/images/column1.png" alt="">
        <p>我的团队</p>
      </router-link>
      <router-link to="myOrder" class="item">
        <img src="../../assets/images/column2.png" alt="">
        <p>团队订单</p>
      </router-link>
      <router-link to="myGold" class="item">
        <img src="../../assets/images/column3.png" alt="">
        <p>我的元宝</p>
      </router-link>
      <router-link to="generalizeCode" class="item">
        <img src="../../assets/images/column4.png" alt="">
        <p>推广二维码</p>
      </router-link>
    </div>
    <div class="block flex-wrap column2">
      <router-link to="orderList" class="item">
        <img src="../../assets/images/columns1.png" alt="">
        <p>我的订单</p>
      </router-link>
      <router-link to="cellularState" class="item">
        <img src="../../assets/images/columns2.png" alt="">
        <p>细胞状态</p>
      </router-link>
      <router-link to="examination" class="item">
        <img src="../../assets/images/columns3.png" alt="">
        <p>体检查询</p>
      </router-link>
      <router-link to="yard" class="item">
        <img src="../../assets/images/columns4.png" alt="">
        <p>进门码</p>
      </router-link>
      <router-link to="appointment" class="item">
        <img src="../../assets/images/columns5.png" alt="">
        <p>预约参观</p>
      </router-link>
      <router-link to="enter" class="item">
        <img src="../../assets/images/columns6.png" alt="">
        <p>机构管理</p>
      </router-link>
      <router-link to="myCollect" class="item">
        <img src="../../assets/images/columns7.png" alt="">
        <p>我的收藏</p>
      </router-link>
      <router-link to="coupon" class="item">
        <img src="../../assets/images/columns8.png" alt="">
        <p>优惠券</p>
      </router-link>
      <router-link to="myEvaluate" class="item">
        <img src="../../assets/images/columns12.png" alt="">
        <p>我的评价</p>
      </router-link>
      <router-link to="address" class="item">
        <img src="../../assets/images/columns9.png" alt="">
        <p>收货地址</p>
      </router-link>
      <router-link to="myNews" class="item">
        <img src="../../assets/images/columns10.png" alt="">
        <p>我的消息</p>
      </router-link>
      <router-link to="help" class="item">
        <img src="../../assets/images/columns11.png" alt="">
        <p>帮助中心</p>
      </router-link>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant';
export default {
  components: {
    'van-icon': Icon
  },
  created() {
    // this.getData()
  },
  methods: {
    getData() {
      this.$post('user/userinfo', {}).then((res) => {
        
      })
    }
  }
}
</script>

<style scoped lang="scss">
.user {
  background: url('src/assets/images/user_bg.png') no-repeat top;
  background-size: 100% auto;
  overflow: hidden;
}
.head {
  margin: 35px 40px;
  position: relative;
  .portrait {
    padding: 5px;
    background: #64c6f7;
    border-radius: 50%;
    img {
      width: 130px;
      height: 130px;
      border-radius: 50%;
    }
  }
  .order {
    color: #fff;
    margin-left: 25px;
    .name {
      font-size: 40px;
    }
    .identity {
      font-size: 26px;
      padding: 2px 14px;
      background: #fff;
      border-radius: 30px;
      color: #2194e6;
      margin-left: 14px;
    }
    .id {
      margin-top: 10px;
      font-size: 28px;
    }
  }
  .setup {
    position: absolute;
    right: 0;
    top: 0;
    width: 40px;
  }
  .signIn {
    position: absolute;
    right: -38px;
    top: 74px;
    height: 56px;
    background: rgba(255, 255, 255, .15);
    color: #fff;
    font-size: 28px;
    padding-left: 30px;
    padding-right: 10px;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    border: 2px solid #6db6ff;
    border-right: 0;
    img {
      width: 29px;
    }
    p {
      margin: 0 4px 0 12px;
    }
    .arrow {
      font-size: 22px
    }
  }
}
.block {
  margin: 25px 40px;
  background: #fff;
  border-radius: 30px;
  padding-left: 30px;
  padding-right: 30px;
  box-shadow: 0 4px 20px #e6e6e6;
  .wallet {
    margin: 30px 0;
    // &:first-child {
    //   border-right: 1px solid #eee;
    // }
    img {
      width: 80px;
    }
    .text {
      margin-left: 25px;
      .num {
        font-size: 34px;
        font-weight: bold;
      }
      .txt {
        color: #939393;
        font-size: 24px
      }
    }
    
  }
}
.column1 {
  .item {
    margin: 30px 0;
    text-align: center;
    flex: 1;
    img {
      width: 90px;
      margin-bottom: 10px;
    }
  }
}
.column2 {
  padding-top: 25px;
  padding-bottom: 25px;
  .item {
    width: 25%;
    margin: 25px 0;
    text-align: center;
    img {
      height: 60px;
      margin-bottom: 10px;
    }
  }
}
</style>


