<template>
  <div class="orderList">
    <van-tabs type="card" class="tab" v-model="active" @change="getData" swipeable>
      <van-tab v-for="(item, index) in tabList" :title="item.title" :key="index">
        <van-list v-model="item.loading" :finished="item.finished" finished-text="没有更多了" @load="getData">
          <div class="list">
            <router-link :to="'/orderDetails?id='+item.id" class="item" v-for="(item, index) in item.list" :key="index">
              <div class="head flex-between">
                <p class="number">订单号：{{item.order_sn}}</p>
                <p class="status" v-if="item.order_status==0" style="color: #f8900a">待付款</p>
                <p class="status" v-if="item.order_status==1" style="color: #2277e2">待发货</p>
                <p class="status" v-if="item.order_status==2" style="color: #858585">已完成</p>
              </div>
              <div class="info flex">
                <img :src="item.original_img" alt="">
                <div class="flex_1">
                  <p class="title">{{item.goods_name}}</p>
                  <p class="text">{{item.goods_name}}</p>
                  <p class="price">￥{{item.goods_price}} <span>x{{item.goods_num}}</span></p>
                </div>
              </div>
              <div class="total flex-center-y">
                总价：<span>￥{{item.order_amount}}</span>
              </div>
              <div class="button flex-center-y">
                <div class="border flex-center">联系客服</div>
                <div class="border flex-center">取消订单</div>
                <div class="background flex-center">付款</div>
                <div class="background_blue flex-center">提现发货</div>
              </div>
            </router-link>
          </div>
        </van-list>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { Tab, Tabs, List } from 'vant';
export default {
  components: {
    'van-tabs': Tabs,
    'van-tab': Tab,
    'van-list': List
  },
  data() {
    return {
      active: 0,
      tabList: [
        {
          title: '全部',
          loading: false,
          page: 1,
          finished: false,
          list: [],
          type: ''
        },
        {
          title: '待付款',
          loading: false,
          page: 1,
          finished: false,
          list: [],
          type: 'waitpay'
        },
        {
          title: '待发货',
          loading: false,
          page: 1,
          finished: false,
          list: [],
          type: 'waitsend'
        },
        {
          title: '配送中',
          loading: false,
          page: 1,
          finished: false,
          list: [],
          type: 'waitccommet'
        },
        {
          title: '已完成',
          loading: false,
          page: 1,
          finished: false,
          list: [],
          type: 'finish'
        }
      ]
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      let index = this.active
      if (this.tabList[index].finished) return false
      this.tabList[index].loading = true
      let data = {
        p: this.tabList[index].page,
        type: this.tabList[index].type
      }
      this.$post('order/order_list', data).then((res) => {
        this.tabList[index].list = this.tabList[index].list.concat(res.data.goods)
        this.tabList[index].loading = false
        if (res.data.goods.length < 10)  this.tabList[index].finished = true
        this.tabList[index].page = ++this.tabList[index].page
      })
    }
  }
}
</script>

<style scoped lang="scss">
.orderList {
  margin-top: 25px;
}
.list {
  .item {
    padding: 0 20px;
    margin: 30px 30px 0 30px;
    background: #fff;
    border-radius: 20px;
    display: block;
    .head {
      height: 100px;
    }
    .info {
      padding: 20px;
      background: #fafafa;
      img {
        width: 120px;
        height: 120px;
        margin-right: 25px;
        border-radius: 6px;
      }
      .flex_1 {
        overflow: hidden;
        .text {
          font-size: 22px;
          color: #848484;
          margin: 6px 0;
        }
        .price {
          span {
            color: #848484;
          }
        }
      }
    }
    .total {
      height: 80px;
      justify-content: flex-end;
      span {
        color: #ef1818;
        font-weight: bold;
      }
    }
    .button {
      justify-content: flex-end;
      padding-bottom: 30px;
      div {
        width: 170px;
        height: 50px;
        border-radius: 30px;
        margin-right: 20px;
        font-size: 22px;
        &:last-child {
          margin-right: 0;
        }
      }
      .border {
        border: 2px solid #efefef;
        box-sizing: border-box;
        color: #545454;
      }
      .background {
        background: linear-gradient(to right, #fcd54e , #edb342);
        color: #fff;
      }
      .background_blue  {
        background: linear-gradient(to right, #21cce6 , #2697e7);
        color: #fff;
      }
    }
  }
}
</style>



