<template>
  <div>
    <div class="wrap">
      <div class="item flex-between head">
        <div class="title">头像</div>
        <van-uploader :after-read="onReads" accept="image/*" >
          <img src="../../assets/images/classify_item.png" alt="" class="upload img">
        </van-uploader>
      </div>
      <div class="item flex-between">
        <div class="title">用户名</div>
        <div class="right flex-center-y" @click="popup('nickname')"><span>令小奇</span><van-icon name="arrow" class="arrow" /></div>
      </div>
      <div class="item flex-between">
        <div class="title">性别</div>
        <div class="right flex-center-y" @click="sexShow = true"><span>男</span><van-icon name="arrow" class="arrow" /></div>
      </div>
      <div class="item flex-between">
        <div class="title">生日</div>
        <div class="right flex-center-y" @click="timeShow = true"><span>1970-10-10</span><van-icon name="arrow" class="arrow" /></div>
      </div>
      <div class="item flex-between">
        <div class="title">手机</div>
        <div class="right flex-center-y" @click="popup('mobile')"><span>13612954888</span><van-icon name="arrow" class="arrow" /></div>
      </div>
      <div class="item flex-between">
        <div class="title">邮箱</div>
        <div class="right flex-center-y" @click="popup('email')"><span>13612954888@163.com</span><van-icon name="arrow" class="arrow" /></div>
      </div>
    </div>

    <!-- 基础修改 -->
    <van-popup v-model="show" position="bottom">
      <div class="popup">
        <van-icon name="cross" @click="show=false" class="cross" />
        <input type="text" v-model="mod">
        <div class="button flex-center" @click="submit">确认修改</div>
      </div>
    </van-popup>

    <!-- 修改性别 -->
    <van-actionsheet v-model="sexShow" :actions="actions" cancel-text="取消" @select="onSelect"/>

    <!-- 修改生日 -->
    <van-popup v-model="timeShow" position="bottom">
      <van-datetime-picker v-model="currentDate" type="datetime" :min-date="new Date(1900, 1, 1)" :max-date="new Date()" @cancel="timeShow=false" @confirm="onConfirm"/>
    </van-popup>
  </div>
</template>

<script>
import { Icon, Popup, Actionsheet, DatetimePicker, Uploader } from 'vant';
export default {
  components: {
    'van-icon': Icon,
    'van-popup': Popup,
    'van-actionsheet': Actionsheet,
    'van-datetime-picker': DatetimePicker,
    'van-uploader': Uploader
  },
  data() {
    return {
      show: false,
      mod: '',
      attribute: '',
      sexShow: false,
      timeShow: false,
      actions: [
        {
          name: '男',
          sex: 1
        },
        {
          name: '女',
          sex: 2
        },
      ],
      currentDate: new Date()
    }
  },
  methods: {
    popup(name) {
      this.show = true
      this.attribute = name
    },
    // 确认修改
    submit() {
      let data = {}
      data[this.attribute] = this.mod
      this.$post('user/userinfo', data).then((res) => {
        // Toast('保存成功')
        // this.$router.go(-1)
      })
    },
    // 修改性别
    onSelect(e) {
      this.$post('user/userinfo', {sex: e.sex}).then((res) => {
        Toast('保存成功')
        this.$router.go(-1)
      })
    },
    // 修改生日
    onConfirm(e) {
      console.log(e.getTime())
      this.$post('user/userinfo', {sex: e.getTime()}).then((res) => {
        // Toast('保存成功')
        // this.$router.go(-1)
      })
    },
    // 图片修改
    onReads(e) {
      
    }
  }
}
</script>

<style scoped lang="scss">
.wrap {
  background: #fff;
  padding: 0 30px;
  .item {
    height: 100px;
    border-bottom: 1px solid #ebebeb;
    font-size: 32px;
    .img {
      width: 120px;
      height: 120px;
      border-radius: 50%;
    }
    .title {
      color: #4f4f4f;
    }
    .right {
      span {
        // color: #959595;
        // font-size: 26px;
        margin-right: 6px;
      }
    }
  }
  .head {
    height: auto;
    padding: 20px 0;
  }
}
.popup {
  padding: 60px 30px;
  box-sizing: border-box;
  text-align: center;
  position: relative;
  .cross {
    position: absolute;
    right: 26px;
    top: 26px;
    font-size: 40px;
  }
  input {
    margin: 60px auto;
    height: 90px;
    border: 2px solid #e0dede;
    width: 96%;
    border-radius: 16px;
    box-sizing: border-box;
    padding: 0 30px;
    font-size: 32px;
  }
  .button {
    margin: 60px auto;
    background: linear-gradient(to bottom, #2194e6 , #21cce6);
    color: #fff;
    height: 90px;
    border-radius: 60px;
    font-size: 38px;
    box-shadow: 0 4px 16px #b3edf6;
  }
}
</style>



