import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/index'

import home from '@/components/home/home'
import details from '@/components/home/details'
import yard from '@/components/home/yard'
import appointment from '@/components/home/appointment'
import cellularState from '@/components/home/cellularState'
import examination from '@/components/home/examination'
import submitOrder from '@/components/home/submitOrder'
import success from '@/components/home/success'
import search from '@/components/home/search'

import cart from '@/components/cart/cart'
import classify from '@/components/classify/classify'

import user from '@/components/user/user'
import myTeam from '@/components/user/myTeam'
import myOrder from '@/components/user/myOrder'
import myGold from '@/components/user/myGold'
import myIntegral from '@/components/user/myIntegral'
import generalizeCode from '@/components/user/generalizeCode'
import orderList from '@/components/user/orderList'
import orderDetails from '@/components/user/orderDetails'
import myCollect from '@/components/user/myCollect'
import myEvaluate from '@/components/user/myEvaluate'
import evaluate from '@/components/user/evaluate'
import myNews from '@/components/user/myNews'
import help from '@/components/user/help'
import enter from '@/components/user/enter'
import coupon from '@/components/user/coupon'
import getCoupon from '@/components/user/getCoupon'
import address from '@/components/user/address'
import mod_address from '@/components/user/mod_address'
import signIn from '@/components/user/signIn'
import settings from '@/components/user/settings'
import user_info from '@/components/user/user_info'
import attestation from '@/components/user/attestation'
import backCrad from '@/components/user/backCrad'
import backCradInput from '@/components/user/backCradInput'
import bindPhone from '@/components/user/bindPhone'
import bindCode from '@/components/user/bindCode'
import paymentCode from '@/components/user/paymentCode'
import feedback from '@/components/user/feedback'
import afterSale from '@/components/user/afterSale'
import afterSale_detail from '@/components/user/afterSale_detail'
import afterSale_detail_path from '@/components/user/afterSale_detail_path'
import afterSale_barter from '@/components/user/afterSale_barter'
import withdrawDeposit from '@/components/user/withdrawDeposit'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index,
      meta: {
        title: '华云生命'
      },
      children: [
        {
          path: 'home',
          name: 'home',
          component: home,
          meta: {
            title: '华云生命'
          },
        },
        {
          path: 'cart',
          name: 'cart',
          component: cart,
          meta: {
            title: '购物车'
          },
        },
        {
          path: 'classify',
          name: 'classify',
          component: classify,
          meta: {
            title: '分类'
          },
        },
        {
          path: 'user',
          name: 'user',
          component: user,
          meta: {
            title: '个人中心'
          },
        }
      ],
      redirect:'/home'
    },
    {
      path: '/details',
      name: 'details',
      component: details,
      meta: {
        title: '详情页'
      },
    },
    {
      path: '/yard',
      name: 'yard',
      component: yard,
      meta: {
        title: '进门码'
      },
    },
    {
      path: '/appointment',
      name: 'appointment',
      component: appointment,
      meta: {
        title: '预约参观'
      },
    },
    {
      path: '/myTeam',
      name: 'myTeam',
      component: myTeam,
      meta: {
        title: '我的团队'
      },
    },
    {
      path: '/myOrder',
      name: 'myOrder',
      component: myOrder,
      meta: {
        title: '推广订单'
      },
    },
    {
      path: '/cellularState',
      name: 'cellularState',
      component: cellularState,
      meta: {
        title: '细胞状态查询'
      },
    },
    {
      path: '/examination',
      name: 'examination',
      component: examination,
      meta: {
        title: '体检查询'
      },
    },
    {
      path: '/myGold',
      name: 'myGold',
      component: myGold,
      meta: {
        title: '我的元宝'
      },
    },
    {
      path: '/myIntegral',
      name: 'myIntegral',
      component: myIntegral,
      meta: {
        title: '我的积分'
      },
    },
    {
      path: '/generalizeCode',
      name: 'generalizeCode',
      component: generalizeCode,
      meta: {
        title: '推广二维码'
      },
    },
    {
      path: '/orderList',
      name: 'orderList',
      component: orderList,
      meta: {
        title: '我的订单'
      },
    },
    {
      path: '/myCollect',
      name: 'myCollect',
      component: myCollect,
      meta: {
        title: '我的收藏'
      },
    },
    {
      path: '/myEvaluate',
      name: 'myEvaluate',
      component: myEvaluate,
      meta: {
        title: '我的评价'
      },
    },
    {
      path: '/evaluate',
      name: 'evaluate',
      component: evaluate,
      meta: {
        title: '商品评价'
      },
    },
    {
      path: '/myNews',
      name: 'myNews',
      component: myNews,
      meta: {
        title: '我的消息'
      },
    },
    {
      path: '/help',
      name: 'help',
      component: help,
      meta: {
        title: '帮助中心'
      },
    },
    {
      path: '/enter',
      name: 'enter',
      component: enter,
      meta: {
        title: '机构申请'
      },
    },
    {
      path: '/coupon',
      name: 'coupon',
      component: coupon,
      meta: {
        title: '优惠劵'
      },
    },
    {
      path: '/getCoupon',
      name: 'getCoupon',
      component: getCoupon,
      meta: {
        title: '领取优惠劵'
      },
    },
    {
      path: '/address',
      name: 'address',
      component: address,
      meta: {
        title: '收货地址'
      },
    },
    {
      path: '/signIn',
      name: 'signIn',
      component: signIn,
      meta: {
        title: '签到送积分'
      },
    },
    {
      path: '/settings',
      name: 'settings',
      component: settings,
      meta: {
        title: '设置'
      },
    },
    {
      path: '/user_info',
      name: 'user_info',
      component: user_info,
      meta: {
        title: '个人资料'
      },
    },
    {
      path: '/attestation',
      name: 'attestation',
      component: attestation,
      meta: {
        title: '实名认证'
      },
    },
    {
      path: '/backCrad',
      name: 'backCrad',
      component: backCrad,
      meta: {
        title: '我的银行卡'
      },
    },
    {
      path: '/backCradInput',
      name: 'backCradInput',
      component: backCradInput,
      meta: {
        title: '修改银行卡'
      },
    },
    {
      path: '/bindPhone',
      name: 'bindPhone',
      component: bindPhone,
      meta: {
        title: '绑定手机号'
      },
    },
    {
      path: '/paymentCode',
      name: 'paymentCode',
      component: paymentCode,
      meta: {
        title: '支付密码'
      },
    },
    {
      path: '/bindCode',
      name: 'bindCode',
      component: bindCode,
      meta: {
        title: '修改密码'
      },
    },
    {
      path: '/submitOrder',
      name: 'submitOrder',
      component: submitOrder,
      meta: {
        title: '提交订单'
      },
    },
    {
      path: '/success',
      name: 'success',
      component: success,
      meta: {
        title: '订单提交成功'
      },
    },
    {
      path: '/search',
      name: 'search',
      component: search,
      meta: {
        title: '搜索'
      },
    },
    {
      path: '/feedback',
      name: 'feedback',
      component: feedback,
      meta: {
        title: '意见反馈'
      },
    },
    {
      path: '/mod_address',
      name: 'mod_address',
      component: mod_address,
      meta: {
        title: '添加地址'
      },
    },
    {
      path: '/orderDetails',
      name: 'orderDetails',
      component: orderDetails,
      meta: {
        title: '订单详情'
      },
    },
    {
      path: '/afterSale',
      name: 'afterSale',
      component: afterSale,
      meta: {
        title: '申请售后'
      },
    },
    {
      path: '/afterSale_detail',
      name: 'afterSale_detail',
      component: afterSale_detail,
      meta: {
        title: '售后详情'
      },
    },
    {
      path: '/afterSale_detail_path',
      name: 'afterSale_detail_path',
      component: afterSale_detail_path,
      meta: {
        title: '订单跟踪'
      },
    },
    {
      path: '/afterSale_barter',
      name: 'afterSale_barter',
      component: afterSale_barter,
      meta: {
        title: '申请换货'
      },
    },
    {
      path: '/withdrawDeposit',
      name: 'withdrawDeposit',
      component: withdrawDeposit,
      meta: {
        title: '申请提现'
      },
    }
  ]
})



