<template>
  <div class="coupon">
    <van-tabs type="card" class="tab" v-model="active" @change="getData" swipeable>
      <van-tab v-for="(item, index) in tabList" :title="item.title" :key="index">
        <van-list v-model="item.loading" :finished="item.finished" finished-text="没有更多了" @load="getData">
          <router-link to="getCoupon" class="get flex-between">
            <div class="left flex-center-y"><img src="../../assets/images/coupon_icon.png" alt="">领劵中心</div>
            <van-icon name="arrow" class="arrow" />
          </router-link>
          <div class="list">
            <div class="item flex-center-y" v-for="(item, index) in item.list" :key="index" :class="item.use_status!=0 ? 'disabled' : ''">
              <div class="left flex-center">
                <div class="num">{{item.expression}}</div>
                <div class="text">{{item.content}}</div>
              </div>
              <div class="center flex_1">
                <p class="title">{{item.name}}</p>
                <p class="time">有效期至 [{{item.end_time}}]</p>
              </div>
              <div class="right flex-center">
                <div class="button flex-center" v-if="item.use_status==0">去使用</div>
                <img src="../../assets/images/disabled.png" alt="" v-else class="img">
              </div>
            </div>
          </div>
        </van-list>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { Tab, Tabs, Icon, List } from 'vant';
export default {
  components: {
    'van-tabs': Tabs,
    'van-tab': Tab,
    'van-icon': Icon,
    'van-list': List
  },
  data() {
    return {
      tabList: [
        {
          title: '未使用',
          loading: false,
          page: 1,
          finished: false,
          list: []
        },
        {
          title: '已使用',
          loading: false,
          page: 1,
          finished: false,
          list: []
        },
        {
          title: '已过期',
          loading: false,
          page: 1,
          finished: false,
          list: []
        }
      ],
      active: 0
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      let index = this.active
      if (this.tabList[index].finished) return false
      this.tabList[index].loading = true
      let data = {
        p: this.tabList[index].page,
        use_status: index
      }
      this.$post('user/coupon_list', data).then((res) => {
        this.tabList[index].list = this.tabList[index].list.concat(res.data)
        this.tabList[index].loading = false
        if (res.data.length < 10)  this.tabList[index].finished = true
        this.tabList[index].page = ++this.tabList[index].page
      })
    }
  }
}
</script>

<style scoped lang="scss">
.coupon {
  margin-top: 30px;
  .get {
    margin: 30px;
    background: #fff;
    border-radius: 20px;
    height: 90px;
    padding: 0 25px;
    font-size: 32px;
    box-shadow: 0 2px 10px #e6e6e6;
    img {
      width: 50px;
      margin-right: 12px;
    }
  }
  .list {
    padding: 0 30px;
    .item {
      background: url('src/assets/images/coupon_bg.png') no-repeat center center;
      background-size: 100% 100%;
      height: 200px;
      margin-top: 20px;
      .left {
        width: 180px;
        color: #fff;
        height: 100%;
        flex-direction: column;
        .num {
          font-size: 60px;
        }
        .text {
          font-size: 22px;
          margin-top: 4px;
        }
      }
      .center {
        overflow: hidden;
        padding: 0 20px;
        .title {
          font-size: 30px;
        }
        .time {
          color: #939393;
          margin-top: 30px;
          font-size: 22px;
        }
      }
      .right {
        width: 170px;
        .button {
          width: 120px;
          height: 40px;
          border-radius: 30px;
          border: 2px solid #2198e6;
          color: #2198e6;
        }
        .img {
          width: 140px;
        }
      }
    }
    .disabled {
      background: url('src/assets/images/coupon_disabled.png') no-repeat center center;
      background-size: 100% 100%;
    }
  }
}
</style>



