<template>
  <div class="myGold">
    <div class="info">
      <div class="flex-between">
        <div class="left">
          <p class="text">元宝余额</p>
          <p class="num">3840.00</p>
        </div>
        <router-link to="/withdrawDeposit" class="button flex-center">提现</router-link>
      </div>
      <div class="flex">
        <div class="item flex_1">
          <p>可提现</p>
          <p>1841.00</p>
        </div>
        <div class="item flex_1">
          <p>结算中</p>
          <p>1841.00</p>
        </div>
      </div>
    </div>
    <div class="list">
      <div class="head">元宝明细</div>
      <div class="item flex-between" v-for="(item, index) in 5" :key="index">
        <div class="left">
          <p class="title">提现</p>
          <p class="time">2019-10-10 10:10:10</p>
        </div>
        <div class="right" :class="index === 0 ? 'plus' : 'minus'">+150.00</div>
      </div>
    </div>
  </div>
</template>

<script>
import { List } from 'vant'
export default {
  components: {
    'van-list': List
  },
  data() {
    return {
      list: [],
      title: [],
      loading: false,
      page: 1,
      finished: false
    }
  },
  created() {
    // this.getData()
  },
  methods: {
    getData() {
      let data = {
        p: this.page,
        level: this.$route.query.level
      }
      this.loading = true
      this.$get('user/account_list', data).then((res) => {
        this.loading = false
        for(let i in res.data.list) {
          let index = this.title.indexOf(i)
          if (this.title.indexOf(i) === -1) {
            this.title.push(i)
            this.list.push(res.data.list[i])
          } else {
            this.list[index] = this.list[index].concat(res.data.list[i])
          }
        }
        res.data.next_page ? this.page = res.data.next_page : this.finished = true
      })
    }
  }
}
</script>

<style scoped lang="scss">
.myGold {
  background: #fff;
}
.info {
  padding: 40px;
  background: url('src/assets/images/gold_bg.png') no-repeat center center;
  background-size: 100% 100%;
  margin: 40px;
  border-radius: 30px;
  color: #fff;
  .num {
    font-size: 70px;
    margin-top: 10px;
  }
  .button {
    width: 120px;
    height: 50px;
    border-radius: 60px;
    background: #fff;
    color: #f3bd3a;
  }
  .flex {
    margin-top: 30px;
    .item:first-child {
      border-right: 1px solid #fff;
      margin-right: 40px;
    }
    p:first-child {
      margin-bottom: 10px;
    }
  }
}
.list {
  margin: 40px;
  .item {
    padding: 30px 0;
    border-bottom: 1px solid #eee;
    .title {
      font-size: 32px;
    }
    .time {
      font-size: 26px;
      color: #a4a4a4;
      margin-top: 15px;
    }
    .right {
      font-size: 40px
    }
    .plus {
      color: #2194e6;
    }
    .minus {
      color: #ecae33;
    }
  }
}
</style>



