<template>
  <div class="cart container">
    <div class="head flex-between">
      <p class="allNum">共{{checked.length}}件商品</p>
      <img src="../../assets/images/cart_del.png" alt="">
    </div>
    <div class="list">
      <van-checkbox-group v-model="checked" @change="select">
        <div class="item flex-center-y" v-for="(item, index) in list" :key="index">
          <van-checkbox class="checkbox" checked-color="#2194e6" :name="index"></van-checkbox>
          <div class="info flex flex_1">
            <img src="../../assets/images/classify_item.png" alt="" class="item">
            <div class="introduce">
              <p class="title ellipsis">{{item.goods_name}}</p>
              <p class="text">{{item.goods_remark}}</p>
              <div class="handle flex-between">
                <p class="price">￥{{item.goods_price}}</p>
                <van-stepper class="flex" v-model="item.goods_num" @change="stepperChange(index)"/>
              </div>
            </div>
          </div>
        </div>
      </van-checkbox-group>
    </div>
    <div class="balance flex-center-y">
      <div class="left flex_1 flex-between">
        <van-checkbox checked-color="#2194e6" @change="all" v-model="checkAll">全选</van-checkbox>
        <p class="total">合计：<span>￥{{total}}</span></p>
      </div>
      <div class="button">去结算</div>
    </div>
  </div>
</template>

<script>
import { Checkbox, CheckboxGroup, Stepper } from 'vant';
export default {
  components: {
    'van-checkbox': Checkbox,
    'van-checkbox-group': CheckboxGroup,
    'van-stepper': Stepper
  },
  data() {
    return {
      checked: [],
      page: 1,
      list: [],
      finished: false,
      checkAll: false,
      total: "0.00"
    }
  },
  created() {
    this.getData()
  },
  methods: {
    getData() {
      if (this.finished) return false
      let data = {
        p: this.page
      }
      this.$post('cart/index', data).then((res) => {
        this.list = this.list.concat(res.data)
        if (res.data.length < 10)  this.finished = true
        this.page = ++this.page
      })
    },
    // 勾选改变
    select() {
      this.countTotal()
    },
    // 计算价格
    countTotal() {
      let total = 0
      this.checked.forEach((val, key) => {
        total += Number(this.list[val].goods_price) * Number(this.list[val].goods_num)
      })
      this.total = total.toFixed(2)
    },
    // 全选
    all() {
      if (this.checkAll) {
        let checked = []
        this.list.forEach((val, key) => {
          checked.push(key)
        })
        this.checked = checked
        this.countTotal()
      } else {
        this.checked = []
        this.countTotal()
      }
    },
    // 数量变动
    stepperChange(index) {
      let data = {
        goods_num: this.list[index].goods_num,
        id: this.list[index].id
      }
      this.$post('cart/changeNum', data, false).then((res) => {
        this.countTotal()
      })
    }
  }
}
</script>

<style scoped lang="scss">
.head {
  padding: 20px 35px;
  font-size: 36px;
  img {
    width: 70px;
    box-shadow: 0 0 20px #aaa;
    border-radius: 50%;
  }
}
.list {
  box-shadow: 0 0 16px #f0e8e8;
  margin: 0 35px 110px 35px;
  background: #fff;
  border-radius: 20px;
  .checkbox {
    margin: 0 20px;
  }
  .info {
    padding: 30px 0;
    border-bottom: 1px solid #eee;
    overflow: hidden;
    &:last-child {
      border-bottom: 0;
    }
    img {
      width: 185px;
      height: 185px;
      border-right: 20px;
    }
    .introduce {
      padding: 0 25px;
      overflow: hidden;
      .title {
        font-size: 34px;
        margin-bottom: 8px;
      }
      .text {
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
        overflow: hidden;
      }
      .handle {
        padding-top: 10px;
      }
      .price {
        color: #dc2121;
      }
    }
  }
}
.balance {
  position: fixed;
  height: 100px;
  bottom: 108px;
  left: 0;
  background: #fff;
  width: 100%;
  box-shadow: 0 0 20px #eeecec;
  .total {
    span {
      font-size: 34px;
      color: #dc2121;
    }
  }
  .left {
    padding: 0 50px;
    height: 100px;
    color: #707070;
  }
  .button {
    width: 250px;
    background: linear-gradient(to right, #21cce6 , #2194e6);
    height: 100px;
    line-height: 100px;
    text-align: center;
    font-size: 36px;
    color: #fff;
    letter-spacing: 2px;
  }
}
</style>





