<template>
  <div class="bindSettings">
    <div class="list">
      <div class="item flex-center-y">
        <div class="img"><img src="../../assets/images/s_unlock.png" alt=""></div>
        <input type="password" placeholder="请输入登录密码" v-model="data.password">
      </div>
      <div class="item flex-center-y">
        <div class="img"><img src="../../assets/images/s_unlock.png" alt=""></div>
        <input type="password" placeholder="请设置支付密码" v-model="data.paypwd">
      </div>
      <div class="item flex-center-y">
        <div class="img"><img src="../../assets/images/s_unlock.png" alt=""></div>
        <input type="password" placeholder="请再次输入密码" v-model="data.two_paypwd">
      </div>
      <div class="button flex-center" @click="submit">确定</div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      data: {
        password: '',
        paypwd: '',
        two_paypwd: ''
      }
    }
  },
  methods: {
    submit() {
      this.$post('user/paypwd', this.data).then((res) => {
        // Toast('保存成功')
        // this.$router.go(-1)
      })
    }
  }
}
</script>

<style scoped lang="scss">
.list {
  padding: 30px 40px;
}
.item {
  height: 120px;
  border-bottom: 1px solid #ebebeb;
   font-size: 32px;
  img {
    width: 36px;
    margin-right: 20px;
  }
  input {
    flex: 1;
  }
  .code {
    width: 170px;
    height: 50px;
    background: linear-gradient(to right, #21cce6 , #2194e6);
    font-size: 22px;
    border-radius: 60px;
    color: #fff;
  }
}
.button {
  margin: 50px auto;
  background: linear-gradient(to right, #21cce6 , #2194e6);
  color: #fff;
  height: 90px;
  border-radius: 60px;
  font-size: 38px;
  box-shadow: 0 4px 16px #b3edf6;
}
</style>



