<template>
  <div class="bindSettings">
    <div class="list">
      <div class="item flex-center-y">
        <div class="title">选择银行：</div>
        <input type="text" placeholder="点击选择" readonly>
        <van-icon name="arrow" class="arrow" />
      </div>
      <div class="item flex-center-y">
        <div class="title">银行卡号：</div>
        <input type="text" placeholder="请输入银行卡号">
      </div>
      <div class="item flex-center-y">
        <div class="title">确认卡号：</div>
        <input type="text" placeholder="请再次输入银行卡号">
      </div>
      <div class="item flex-center-y">
        <div class="title">开户所在地：</div>
        <input type="text" placeholder="点击选择" readonly>
         <van-icon name="arrow" class="arrow" />
      </div>
       <div class="item flex-center-y">
        <div class="title">开户行：</div>
        <input type="text" placeholder="请输入开户支行">
      </div>
      <div class="button flex-center">修改</div>
      <div class="button flex-center disabled">删除</div>
    </div>
  </div>
</template>

<script>
import { Icon } from 'vant';
export default {
  components: {
    'van-icon': Icon
  },
}
</script>

<style scoped lang="scss">
.list {
  padding: 30px 40px;
}
.item {
  height: 100px;
  border-bottom: 1px solid #ebebeb;
  font-size: 32px;
  .title {
    width: 200px;;
  }
  input {
    flex: 1;
  }
  .code {
    width: 170px;
    height: 50px;
    background: linear-gradient(to right, #21cce6 , #2194e6);
    font-size: 22px;
    border-radius: 60px;
    color: #fff;
  }
  .arrow {
    color: #afafaf;
  }
}
.button {
  margin: 40px auto;
  background: linear-gradient(to right, #21cce6 , #2194e6);
  color: #fff;
  height: 90px;
  border-radius: 60px;
  font-size: 38px;
  box-shadow: 0 4px 16px #b3edf6;
}
.disabled {
  background: #eee;
  color: #333;
  box-shadow: 0 4px 16px #eee;
}
</style>



